.search--results-heading {
    font-size: .6875rem;
    font-weight: var(--fw-medium);
    color: #000;
    margin: .5em 0;
}

.results {
    padding: 1em 2em;
    background: #F7FAFE;
    border-radius: 3px;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(17rem, 1fr));
    gap: 1em;
}