.tab {
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
    align-items: center;
}

.tab--link {
    font-size: 1.0625rem;
    display: inline-block;
    padding: .75em 1.5em;
    text-decoration: none!important;
    color: var(--text-headline);
    position: relative;
}

.tab--link.active {
    font-weight: var(--fw-medium);
}

.tab--link.active::before {
    content: "";
    width: 60%;
    height: 3px;
    background: var(--clr-blue);
    position: absolute;
    left: 50%;
    bottom: 0;
    transform: translateX(-50%);
}
