.profile {
    display: flex;
    align-items: center;
}

.profile > * + * {
    margin-left: .85em;
}

.username, .role {
    margin: 0;
    padding: 0;
}

.username {
    font-size: .8125rem;
    font-weight: var(--fw-medium);
    color: var(--text-headline);
}

.role {
    font-size: .6875rem;
    color: var(--text-placeholder);
    font-weight: var(--fw-medium);
}