.sidebar {
    grid-area: sidebar;
    display: flex;
    flex-direction: column;
    align-items: center;
    background: #fff;
    box-shadow: 2px 0px 8px -2px rgba(28, 54, 84, 0.2);
}

.company--profile {
    display: flex;
    align-items: center;
    margin: 2em 0;
}

.company--profile>*+* {
    margin-left: .85em;
}

.company--name,
.tagline {
    margin: 0;
    padding: 0;
}

.company--name {
    font-size: .8125rem;
    color: var(--text-headline);
    font-weight: var(--fw-medium);
}

.tagline {
    font-size: .6875rem;
    font-weight: var(--fw-light);
    color: var(--text-placeholder);
}

.sidebar--items {
    width: 100%;
}

.sidebar--links {
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
    flex-direction: column;
    width: 100%;
}

.sidebar--link {
    padding: .75em 1.5em;
    width: 100%;
    display: flex;
    align-items: center;
}

.empty::before {
    content: "";
    height: 15px;
    width: 100px;
    background: var(--text-clr-highlight);
    opacity: .2;
    margin-left: 1.85em;
}

.sidebar--link>*+* {
    margin-left: 1em;
}

.sidebar--link-icon {
    margin-left: 1.65em;
}

.sidebar--link.active {
    background: rgb(11, 80, 218, .2);
    color: var(--clr-blue);
    border-left: 5px solid var(--clr-blue);
}

.title {
    font-size: 1rem;
    font-weight: var(--fw-medium);
}