.header {
    grid-area: navbar;
    display: flex;
    align-items: center;
    padding: .75em 1.5em;
    background: #fff;
    box-shadow: 0px 2px 8px -2px rgba(28, 54, 84, 0.1);
    z-index: 999;
}

.main--nav {
    flex-grow: 1;
}

.logo {
    font-size: 1.5rem;
    color: var(--clr-blue);
    margin: 0;
}

.nav--items {
    list-style: none;
    display: flex;
    align-items: center;
    margin: 0;
}

.nav--items>*+* {
    margin-left: 1.5em;
}

.nav--item:nth-of-type(1) {
    margin: 0 0 0 auto;
}

.nav--link {
    display: flex;
    align-items: center;
    justify-content: center;
}

.nav--link img {
    width: 1rem;
}