.progressbar {
    width: 100%;
}

.indicator {
    width: 100%;
    height: .7rem;
    position: relative;
    background: #f2f2f2;
    border-radius: 100vw;
}

.indicator--inner {
    width: 50%;
    background: var(--clr-green);
    border-radius: 1.25rem;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
}

.progress--count {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: .5em 0;
}

.left, .right {
    font-size: .6875rem;
    font-weight: var(--fw-medium);
}