.search {
    display: flex;
    align-items: center;
}

.search>*+* {
    margin-left: 1em;
}

.search--field {
    padding: .65em 1.5em;
    border: 1px solid #D5D7DD;
    border-radius: 3px;
    font-size: .8125rem;
    color: var(--text-placeholder);
}

.search--btn {
    padding: .625em 1.5em;
    background: rgba(11, 115, 218, 0.3);
    border-radius: 5px;
    font-size: .8125rem;
    color: var(--text-clr-highlight);
    border: none;
}

.search--filter {
    background: rgba(28, 54, 84, 0.3);
    border-radius: 3px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: .625em 1em;
    font-size: .8125rem;
    border: none;
}

.search--filter img {
    display: block;
    margin-left: .5em;
}

.clear {
    font-size: .6875rem;
    color:#EF5252;
}