:root {
  --clr-bg-base: #E5E5E5;
  --clr-blue: #0b50da;
  --clr-green: #6BFA8B;
  --clr-orange: #EF5252;

  --text-clr-highlight: #1773CF;
  --text-headline: #243242;
  --text-placeholder: #5C6370;

  --ff-base: "Roboto", sans-serif;

  --fw-bold: 900;
  --fw-medium: 500;
  --fw-regular: 400;
  --fw-lighter: 100;
  --fw-light: 300;
}

*, *::before, *::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html, body {
  font-family: var(--ff-base);
  background: var(--clr-bg-base);
  line-height: 1.6;
}

img {
  max-width: 100%;
  display: block;
  height: auto;
}
