.board {
    display: flex;
    flex-direction: column;
    padding: 1em;
    background: var(--bg);
    background-position: center;
    /* background-size: contain; */
    background-repeat: no-repeat;
    border-radius: .1875rem;
    color: var(--fg);
}

.board>*+* {
    margin-top: 1.5em;
}

.board--header {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.board--title {
    font-size: 1.1875rem;
    font-weight: var(--fw-medium);
}

.branch p {
    font-size: 0.6875rem;
    margin: 0;
    padding: 0;
}

.branch--numbers {
    font-size: 0.9375rem;
    font-weight: var(--fw-medium);
    display: block;
    text-align: right;
}

.board--stats {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.stat--title {
    font-size: 0.6875rem;
    margin: 0;
    padding: 0;
}

.stat--count {
    font-size: 0.9375rem;
    font-weight: var(--fw-medium);
    display: block;
    text-align: right;
}

.board--blue {
    --fg: #fff;
    /* --bg: url("../../../assets/media/blue-bg.png"); */
    --bg: #2671d9;
}

.board--yellow {
    --fg: #333;
    /* --bg: url("../../../assets/media/yellow-bg.png"); */
    --bg: #fcbb18;
}

.board--purple {
    --fg: #fff;
    /* --bg: url("../../../assets/media/purple-bg.png"); */
    --bg: #9e27b2;
}