.target--card {
    display: flex;
    flex-direction: column;
    padding: 1em;
    box-shadow: 2px 2px 8px -2px rgba(0, 0, 0, 0.1);
    border-radius: 3px;
    background: #fff;
}

.target--card>*+* {
    margin-top: 1em;
}

.card--heading {
    font-size: .8125rem;
    font-weight: var(--fw-medium);
    line-height: 1.1;
    margin: 0;
    padding: 0;
}

.location {
    display: flex;
    align-items: center;
    margin: .5em 0;
}

.location--icon {
    margin-right: .5em;
}

.location--name {
    font-size: .6875rem;
    color: var(--clr-blue);
}

.target--amounts {
    display: flex;
    flex-wrap: wrap;
    gap: 1.9em;
}

.target--heading {
    font-size: .6875rem;
    font-weight: var(--fw-light);
    color: var(--text-placeholder);
    margin: 0;
    padding: 0;
    line-height: 1.1;
}

.amount {
    font-size: .8125rem;
    font-weight: var(--fw-medium);
    color: var(--text-headline);
    margin: .5em 0;
}

.yellow--border {
    border: 2px solid rgba(252, 187, 24, 0.6);
}

.green--border {
    border: 2px solid rgba(107, 250, 139, 0.6);
}

.red--border {
    border: 2px solid rgba(239, 82, 82, 0.6);
}